@import '../../scss/variables';

.sort {
  position: relative;
  &__label {
    display: flex;
    align-items: center;

    svg {
      margin-right: 8px;
    }

    b {
      margin-right: 8px;
    }

    span {
      color: $mainColor;
      border-bottom: 1px dashed $mainColor;
      cursor: pointer;
    }

    @media (max-width: 350px) {
      b {
        display: none;
      }
    }
  }

  &__popup {
    position: absolute;
    right: 0;
    margin-top: 15px;
    background: #ffffff;
    box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.09);
    border-radius: 10px;
    overflow: hidden;
    padding: 10px 0;
    width: 230px;
    ul {
      overflow: hidden;
      li {
        padding: 12px 20px;
        cursor: pointer;

        &.active,
        &:hover {
          background: #f3f4f6;
        }

        &.active {
          font-weight: bold;
          color: $mainColor;
        }
      }
    }
  }
}
